* {
    padding: 0;
    margin: 0;
    background: var(--background-color);
    font-family: var(--font-family);
}

.app-main {
    display: flex;
    flex-direction: column;
    
    width: 100%;
    min-height: 100vh;
}

@media screen and (min-width: 1024px) {
    .app-main {
        display: grid;
        grid-template-columns: 1fr 5fr;
    }
}